<template>
  <nav class="nav" v-if="!nav_state" @mouseover="nav_state = true">
    <v-row class="nav_con">
      <v-col class="nav_logo" cols="4">
        <img src="../../assets/logo/whitelogo.svg" alt="logo" class="blogo" />
      </v-col>
      <v-spacer />
      <v-col cols="8" class="menu_con">
        <div class="menu_list">
          <template v-for="(item, i) in menus" :key="i">
            <router-link :to="item.value">
              <v-list-item :value="i">
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
            </router-link>
          </template>
        </div>
      </v-col>
      <v-col cols="8" class="small_nav">
        <div class="nav_btn">
          <v-icon clas>mdi-menu</v-icon>
        </div>
      </v-col>
    </v-row>
  </nav>
  <ScrollNav v-if="nav_state" @nav_state="updateNavState" />
</template>

<script>
import { reactive } from "vue";
import ScrollNav from "../header/ScrollNav.vue";

export default {
  name: "HeaderNav",
  components: {
    ScrollNav,
  },
  data() {
    return {
      nav_state: false,
    };
  },

  setup() {
    const state = reactive({
      menus: [
        {
          title: "회사소개",
          value: "/Intro",
        },
        {
          title: "회사소식",
          value: "",
        },
        {
          title: "서비스",
          value: "",
        },
        {
          title: "문의&의뢰",
          value: "",
        },
        {
          title: "채용",
          value: "",
        },
      ],
    });

    return state;
  },

  mounted() {
    const miscalc = 3; // 오차 범위

    window.addEventListener("wheel", (e) => {
      if (window.innerHeight > window.scrollY + miscalc) {
        this.nav_state = false;
      }
      if (e.wheelDeltaY < 0) {
        this.nav_state = true;
      }
    });

    this.$anime({
      targets: [".nav"],
      top: "0px",
      duration: 200,
      easing: "easeInSine",
    });
  },
  methods: {
    updateNavState(newState) {
      if (window.scrollY === 0) {
        this.nav_state = newState;
      }
    },
    // mouseleaveheader() {
    //   if (window.scrollY === 0) {
    //     this.nav_state = false;
    //   }
    // },
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1080px) {
  .nav_con {
    width: 68vw;
  }
  .small_nav {
    display: none;
  }
  .blogo {
    width: 160px;
    height: 50px;
  }
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1080px) {
  .nav_con {
    width: 68vw;
  }
  .menu_con {
    display: flex;
  }
  .small_nav {
    display: none;
  }
  .blogo {
    width: 140px;
    height: 40px;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .nav_con {
    width: 100vw;
  }
  .menu_con {
    display: none;
  }
  .small_nav {
    display: flex;
  }
  .blogo {
    width: 120px;
    height: 30px;
  }
}
// 휴대폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .nav_con {
    width: 100vw;
  }
  .small_nav {
    display: flex;
  }
  .menu_con {
    display: none;
  }
  .blogo {
    width: 100px;
    height: 24px;
  }
  .nav_btn {
    font-size: 20px;
  }
}

.nav {
  z-index: 999;
  position: fixed;
  height: 55px;
  width: 100vw;
  top: 0;
  // box-shadow: 1px 0px 5px 0px gray;
}
.small_nav {
  justify-content: right;
  align-items: center;
  width: 100%;
  height: 100%;
  .nav_btn {
    color: white;
    margin: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.nav_con {
  position: relative;
  height: 100%;
  margin: auto;
  .nav_logo {
    cursor: pointer;
    display: flex;
    align-items: center;
    a {
      display: flex;
      align-items: center;
    }
  }
}

.menu_con {
  align-self: center;
  .menu_list {
    position: relative;
    display: flex;
    justify-content: flex-end;
  }
  .menu_list a {
    text-decoration: none;
  }

  .menu_list .v-list-item-title {
    font-family: "Pretendard-Regular";
    font-size: 1.125rem;
    color: rgb(255, 255, 255);
  }
}
// 노트북
@media screen and(min-width: 760px) and(max-width:1080px) {
  .nav_con {
    width: 100vw;
  }
  .small_nav {
    display: none;
  }
  .menu_con {
    display: flex;
    justify-content: right;
    .menu_list {
      padding-right: 16px;
    }
  }
}
// 태블릿 & 핸드폰
@media screen and(min-width: 360px) and(max-width:760px) {
